/**
 * 
 */
function bubbleSort(arr) {
  	let i = 0, len = arr.length, j, d;
  	for (; i < len; i++) {
    	for (j = 0; j < len; j++) {
      		if (arr[i] < arr[j]) {
        		d = arr[j]; arr[j] = arr[i]; arr[i] = d;
      		}
    	}
  	}
  	return arr;
}

export {
	bubbleSort
}



