import {post, upload} from '../axios/ajax'

export const urls = {
  createCommand: '/command/addCommand',   //
  myCommandList: '/command/myCommandPerson',   //
  getCommandList: '/command/getList',   //
  getCommandDetailById: '/command/searchOne',   //
  getCommandPerson: '/command/getCommandPerson',    //
  transpondComandSubmit: '/command/InstructionCommand',     //
  finishCommand: '/command/finishCommand'  //
}

/**
 * 
 * @param params
 * @param callback
 */
export const sendCommand = (params, callback) => { upload(urls.createCommand, params, callback) }

/**
 * 
 * @param params
 * @param callback
 */
export const myCommandList = (params, callback) => { post(urls.myCommandList, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 
 * @param params
 * @param callback
 */
export const getCommandList = (params, callback) => { post(urls.getCommandList, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 
 * @param params
 * @param callback
 */
export const getCommandDetailById = (params, callback) => { post(urls.getCommandDetailById, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 
 * @param params
 * @param callback
 */
export const getCommandPerson = (params, callback) => { post(urls.getCommandPerson, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 
 * @param params
 * @param callback
 */
export const transpondComandSubmit = (params, callback) => { post(urls.transpondComandSubmit, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 
 * @param params
 * @param callback
 */
export const finishCommand = (params, callback) => { upload(urls.finishCommand, params, callback) }



