import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations';
import actions from './actions';
import getters from './getters';

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		isShow: false,//
		show: false,//
		userInfo: {},//
    token: '',   //token
		direction: 'slide-left',
		updateTip: true, //
    buttonList:[]
	},
  modules: {
  },
  mutations: mutations,
  actions: actions,
  getters: getters
});



