import {get, post, upload} from '../axios/ajax'

export const urls = {
  pollingList: '/Patrol/getList',    //
  createPolling: '/Patrol/add',       //
  showPollingDetail: '/Patrol/chakan',        //
  getUserDepartList: '/organizate/depart',      //
  getProjectItemListByOrgId: '/projectItemInfo/getAppList'   //id
}

/**
 * 
 * @param params
 * @param callback
 */
export const getPollingList = (params, callback) => { post(urls.pollingList, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * 
 * @param params
 * @param callback
 */
export const createPolling = (params, callback) => { upload(urls.createPolling, params, callback) }

/**
 * 
 * @param params
 * @param callback
 */
export const getPollingDetail = (params, callback) => { post(urls.showPollingDetail + '/' + params, {}, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }

/**
 * ztree
 * @param callback
 */
export const getUserDepartZtree = (callback) => { get(urls.getUserDepartList, {}, {}, callback)}

/**
 * id
 * @param params
 * @param callback
 */
export const getProjectItemListByOrgId = (params, callback) => { post(urls.getProjectItemListByOrgId, params, {'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'}, callback) }



