<template>
  <div class="has_nav_container">
    <PubNavBar title=""></PubNavBar>
    <div class="syjc_main">
      <div class="syjc_form">
        <div class="syjc_form_item">
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left">:</span>
              <yd-input slot="right" v-model="appointCheckPlan.planSelfCheckPerson" placeholder="" required :readonly="true" @click.native="selectPlanSelfCheckPerson"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">:</span>
              <yd-datetime slot="right" type="date" v-model="appointCheckPlan.planSelfCheckTime" :start-date="defaultPlanCheckTime" required placeholder="" ></yd-datetime>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">:</span>
              <yd-input slot="right" v-model="appointCheckPlan.planCheckPerson" placeholder="" :readonly="true" required @click.native="selectPlanCheckPerson"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left">:</span>
              <yd-datetime slot="right" type="date" v-model="appointCheckPlan.planCheckTime" :start-date="defaultPlanCheckTime" required placeholder=""></yd-datetime>
            </yd-cell-item>
          </yd-cell-group>
        </div>
        <div class="syjc_bot">
          <yd-button size="large" type="primary" shape="circle" @click.native="setCheckPlan"></yd-button>
        </div>
      </div>
    </div>
    <!--    -->
    <yd-popup v-model="isShowPlanSelfCheckPerson" position="bottom" height="90%">
      <yd-infinitescroll :callback="loadSelfCheckPersonList" ref="infinitescrollPlanSelfCheck">
        <yd-list theme="4" slot="list">
          <yd-list-item v-for="(item, key) in planSelfCheckPersonList" :key="key" style="height: 45px;">
            <div slot="img" style=" margin-top: -50px; margin-left: 10px;">
              <yd-checkbox-group v-model="planSelfCheckPersonCheckId" :callback="selectedSelfCheckPersons">
                <yd-checkbox :val="item.id+','+item.username" shape="circle">{{item.username}}</yd-checkbox>
              </yd-checkbox-group>
            </div>
            <div slot="other">
              <span style="font-weight: bold;">:</span>{{item.mobilePhone}}
              <br/>
              <span style="font-weight: bold;">:</span>{{item.zhiwei}}
            </div>
          </yd-list-item>
        </yd-list>
        <span slot="doneTip"></span>
      </yd-infinitescroll>
    </yd-popup>

    <!--    -->
    <yd-popup v-model="isShowPlanCheckPerson" position="bottom" height="90%">
      <yd-infinitescroll :callback="loadCheckPersonList" ref="infinitescrollPlanCheck">
        <yd-list theme="4" slot="list">
          <yd-list-item v-for="(item, key) in planCheckPersonList" :key="key" style="height: 45px;">
            <div slot="img" style=" margin-top: -50px; margin-left: 10px;">
              <yd-checkbox-group v-model="planCheckPersonCheckId" :callback="selectedCheckPersons">
                <yd-checkbox :val="item.id+','+item.username" shape="circle">{{item.username}}</yd-checkbox>
              </yd-checkbox-group>
            </div>
            <div slot="other">
              <span style="font-weight: bold;">:</span>{{item.mobilePhone}}
              <br/>
              <span style="font-weight: bold;">:</span>{{item.zhiwei}}
            </div>
          </yd-list-item>
        </yd-list>
        <span slot="doneTip"></span>
      </yd-infinitescroll>
    </yd-popup>
  </div>
</template>

<script>
import {formatDate} from '../../../assets/js/assist'
import {getNoDelUsersByDepartId, setPlanCheckPerson} from '../../../api/processCheck'
export default {
  data () {
    return {
      planSelfCheckPersonCheckId:[],   //id
      planCheckPersonCheckId: [],  //id
      appointCheckPlan: {
        planSelfCheckPersonId: '',  //
        planCheckPersonId: '',   // id
        processMainId: '',
        planSelfCheckPerson: '',
        planSelfCheckTime: '',
        planCheckPerson: '',
        planCheckTime: ''
      },
      userGroupId: '',  //id
      parentUserGroupId: '',  //id
      defaultPlanCheckTime: '',  //
      isShowPlanSelfCheckPerson: false,  //
      planSelfCheckPersonList: [],  //
      isShowPlanCheckPerson: false,   //
      planCheckPersonList: [],  //
      planSelftCheckPageForm: {  //---
        pageNo: 1,
        pageSize: 10,
        userGroupId: '',
        realname: '',
        position: ''
      },
      planCheckPageForm: {   //---
        pageNo: 1,
        pageSize: 10,
        userGroupId: '',
        realname: '',
        position: ''
      }
    }
  },
  created: function () {
    let strArr = this.$route.query.processMainId
    this.appointCheckPlan.processMainId = strArr.split(',')[0]
    this.userGroupId = this.$route.query.userGroupId;
    this.parentUserGroupId = this.$route.query.parentUserGroupId;
    this.defaultPlanCheckTime = formatDate((new Date()))
  },
  methods: {
    selectPlanSelfCheckPerson(){  //
      this.isShowPlanSelfCheckPerson=true;
      this.planSelftCheckPageForm.pageNo=1;  //
      this.loadSelfCheckPersonList();
    },
    loadSelfCheckPersonList(){  //
      this.$refs.infinitescrollPlanSelfCheck.$emit('ydui.infinitescroll.reInit');
      getNoDelUsersByDepartId({pageNo: this.planSelftCheckPageForm.pageNo, pageSize: this.planSelftCheckPageForm.pageSize,
        userGroupId: this.userGroupId, realname: this.planSelftCheckPageForm.realname, position: this.planSelftCheckPageForm.position}, res=>{
        if(res.ok){
          if(res.data && res.data.data.length > 0){
            const _list = res.data.data;
            this.planSelfCheckPersonList = [..._list, ...this.planSelfCheckPersonList];
            //  
            this.$refs.infinitescrollPlanSelfCheck.$emit('ydui.infinitescroll.finishLoad');
            this.planSelftCheckPageForm.pageNo++;
          }else{
            // /
            this.$refs.infinitescrollPlanSelfCheck.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      });
    },
    selectedSelfCheckPersons(e){   //
      if(e.length>1){
        this.$dialog.toast({
          mes: '',
          timeout: 1500,
          icon: 'error'
        })
        return false
      }else if(e.length===1){
        this.appointCheckPlan.planSelfCheckPerson=e.toString().split(',')[1];
        this.appointCheckPlan.planSelfCheckPersonId=e.toString().split(',')[0];
        this.isShowPlanSelfCheckPerson=false  //
        return true
      }
    },
    selectPlanCheckPerson(){   //
      this.isShowPlanCheckPerson=true;
      this.planCheckPageForm.pageNo=1;  //
      this.loadCheckPersonList();
    },
    loadCheckPersonList(){   //
      this.$refs.infinitescrollPlanCheck.$emit('ydui.infinitescroll.reInit');
      getNoDelUsersByDepartId({pageNo: this.planCheckPageForm.pageNo, pageSize: this.planCheckPageForm.pageSize,
        userGroupId: this.parentUserGroupId, realname: this.planCheckPageForm.realname, position: this.planCheckPageForm.position}, res=>{
        if(res.ok){
          if(res.data && res.data.data.length > 0){
            this.planCheckPersonList = res.data.data;
            //  
            this.$refs.infinitescrollPlanCheck.$emit('ydui.infinitescroll.finishLoad');
            this.planCheckPageForm.pageNo++;
            console.log(this.planCheckPersonList);
          }else{
            // /
            this.$refs.infinitescrollPlanCheck.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      });
    },
    selectedCheckPersons(e){   //
      if(e.length>1){
        this.$dialog.toast({
          mes: '',
          timeout: 1500,
          icon: 'error'
        })
        return false
      }else if(e.length===1){
        this.appointCheckPlan.planCheckPerson=e.toString().split(',')[1];
        this.appointCheckPlan.planCheckPersonId=e.toString().split(',')[0];
        this.isShowPlanCheckPerson=false  //
        return true
      }
    },
    setCheckPlan(){
      setPlanCheckPerson({processId: this.appointCheckPlan.processMainId, planSelfCheckTime: this.appointCheckPlan.planSelfCheckTime,
        planSelfCheckPerson: this.appointCheckPlan.planSelfCheckPersonId, planCheckTime: this.appointCheckPlan.planCheckTime, planCheckPerson: this.appointCheckPlan.planCheckPersonId}, res=>{
        if(res.ok){
          this.$dialog.toast({
            mes: '',
            timeout: 1500
          })
          this.$router.push('/checkPlan')
        }else{
          this.$dialog.toast({
            mes: '',
            timeout: 2000,
            icon: 'error'
          })
        }
      })
    }
  }
}
</script>

<style scoped>
</style>



