import { xhrrequest } from '../axios/ajax'
const properties = require('../assets/js/properties')
import store from '../store/index'

const urls = {
	check: properties.pgyAddress + '/apiv2/app/check'
}

var callback = null
/**
 * 
 */
function versionready(){
	let buildVersion = plus.runtime.version
	let platform = plus.os.name
	if(platform.toLowerCase() == 'android'){
		//android

		xhrrequest("POST", urls.check
				+ '?_api_key=' + properties.pgyKeys.apiKey
				+ '&appKey=' + properties.pgyKeys.androidAppKey
				+ '&buildVersion=' + buildVersion, {},
			(res) => {
			if(res.data.buildHaveNewVersion && res.data.buildVersion != buildVersion){
				plus.nativeUI.confirm( ""+res.data.buildVersion+"", function(i){
					if ( 0==i.index ) {
						//plus.runtime.openURL( res.data.downloadURL );

						let dtask = plus.downloader.createDownload( res.data.downloadURL, {}, function ( d, status ) {
							if ( status == 200 ) { // 
								var path = d.filename;
								console.log(d.filename);
								plus.runtime.install(path);  // apk
							} else {//
								console.log( ": " + status );
							}
						});
						dtask.start();
						store.commit('changeUpdateTip', false)
					} else {
						//
						store.commit('changeUpdateTip', false)
					}
				}, "", ["", " "] );
			}else{
				if(callback) {
          callback()
        }
			}
		})
	}else if(platform.toLowerCase() == 'ios'){
		//ios
		xhrrequest("POST", urls.check
				+ '?_api_key=' + properties.pgyKeys.apiKey
				+ '&appKey=' + properties.pgyKeys.iosAppKey
				+ '&buildVersion=' + buildVersion, {},
			(res) => {
			if(res.data.buildHaveNewVersion && res.data.buildVersion != buildVersion){
				plus.nativeUI.confirm( ""+res.data.buildVersion+"", function(i){
					if ( 0==i.index ) {
						plus.runtime.openURL( res.data.downloadURL );
						store.commit('changeUpdateTip', false)
					} else {
						//
						store.commit('changeUpdateTip', false)
					}
				}, "", ["", " "] );
			}else{
				if(callback) {
          callback()
        }
			}
		})
	}

}
export const checkVersion = (cb) => {
	callback = cb
	if(window.plus){
		versionready();
	}else{
		document.addEventListener('plusready', versionready, false);
	}
}



