import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'

Vue.use(Router)

import Tabs from '../pages/Tabs'  //tab
import Login from '../pages/Login'  //
/**  ***/
import supervisorLog from '../pages/supervisorLog/supervisorLog'  //
import createLog from '../pages/supervisorLog/createLog'   //
import supervisorLogDetail from '../pages/supervisorLog/supervisorLogDetail'  //
/**  ***/
import pollingTab from '../pages/polling/pollingTab'  //
import pollingList from '../pages/polling/polling/pollingList'     //
import createPolling from '../pages/polling/polling/polling'    //
import pollingDetail from '../pages/polling/polling/pollingDetail'  //
/**  ***/
import sideStationList from '../pages/polling/sideStation/sideStationList'   //
import createSideStation from '../pages/polling/sideStation/sideStation'  //
import sideStationDetail from '../pages/polling/sideStation/sideStationDetail'   //
/**  ***/
import processTab from '../pages/process/processTabs'    //
//--
import checkPlan from '../pages/process/appointCheck/checkPlan'   //
import appointCheck from '../pages/process/appointCheck/appointCheck'   //
import selectCheckPerson from '../pages/process/appointCheck/selectCheckPerson'   //
//
import processCheckTabs from '../pages/process/processCheck/processCheckTabs'  //
import receiveProcessTask from '../pages/process/processCheck/receiveProcessTask'    //---
import sendProcessTask from '../pages/process/processCheck/sendProcessTask'    //---
import selfCheckTask from '../pages/process/processCheck/operate/selfCheckTask'   //
import checkTask from '../pages/process/processCheck/operate/checkTask'   //
//
import processSearch from '../pages/process/processSearch/processSearch'   //
import processDetail from '../pages/process/processDetail'   //
/**  ***/
import commandTabs from '../pages/command/commandTabs'    //
//
import myCommand from '../pages/command/myCommand/myCommandList'  //
import createCommand from '../pages/command/myCommand/createCommand'    //
//
import commandTaskTabs from '../pages/command/commandTask/commandTaskTabs'    //
import receiveCommandTask from '../pages/command/commandTask/receiveCommandTask'   //
import sendCommandTask from '../pages/command/commandTask/sendCommandTask'  //
import transpondCommand from '../pages/command/commandTask/operate/transpondCommand'  //
import finishCommand from '../pages/command/commandTask/operate/finishCommand'   //
//
import commandSearch from '../pages/command/commandSearch/commandSearch'    //
import commandDetail from '../pages/command/commandDetail'   //

/**  **/
import footprint from '../pages/footprint/footprint'   //
/**  **/
import messageList from '../pages/message/messageList'  //
/**  **/
import noticeList from '../pages/notice/noticeList'   //
import createNotice from '../pages/notice/createNotice'  //
import noticeDetail from '../pages/notice/noticeDetail'  //



const router = new Router({
  routes: [{
    path: '/', name: 'Login', component: Login, meta:{ title:""}
  }, {
	  path: '/tab', name: 'Tabs', component: Tabs, meta: {title: ''}
  }, {
    path: '/supervisorLog', name: 'supervisorLog', component: supervisorLog, meta: {title: ''}
  }, {
    path: '/createLog', name: 'createLog', component: createLog, meta: {title: ''}
  }, {
    path: '/supervisorLogDetail/:logId', name: 'supervisorLogDetail', component: supervisorLogDetail, props: true, meta: {title: ''}
  }, {
    path: '/pollingTab', name: 'pollingTab', component: pollingTab, meta: {title: ''}
  }, {
    path: '/pollingList', name: 'pollingList', component: pollingList, meta: {title: ''}
  }, {
    path: '/sideStationList', name: 'sideStationList', component: sideStationList, meta: {title: ''}
  }, {
    path: '/createSideStation', name: 'createSideStation', component: createSideStation, meta: {title: ''}
  }, {
    path: '/sideStationDetail/:pollingId', name: 'sideStationDetail', component: sideStationDetail, props: true, meta: {title: ''}
  }, {
    path: '/createPolling', name: 'createPolling', component: createPolling, meta: {title: ''}
  }, {
    path: '/pollingDetail/:pollingId', name: 'pollingDetail', component: pollingDetail, props: true, meta: {title: ''}
  }, {
    path: '/processTab', name: 'processTab', component: processTab, meta: {title: ''}
  }, {
    path: '/processCheckTabs', name: 'processCheckTabs', component: processCheckTabs, meta: {title: 'tabItem'},
    children: [
      { path: '', component: receiveProcessTask, name: '', meta: {title: ''} },
      {path: '/receiveProcessTask', name: 'receiveProcessTask', component: receiveProcessTask, meta: {title: ''} },
      {path: '/sendProcessTask', name: 'sendProcessTask', component: sendProcessTask, meta: {title: ''} }
    ]
  }, {
    path: '/checkPlan', name: 'checkPlan', component: checkPlan, meta: {title: ''}
  }, {
    path: '/appointCheck', name: 'appointCheck', component: appointCheck, meta: {title: ''}
  }, {
    path: '/selectCheckPerson', name: 'selectCheckPerson', component: selectCheckPerson, meta: {title: ''}
  }, {
    path: '/processDetail', name: 'processDetail', component: processDetail, meta: {title: ''}
  },{
    path: '/processSearch', name: 'processSearch', component: processSearch, meta: {title: ''}
  }, {
    path: '/selfCheckTask', name: 'selfCheckTask', component: selfCheckTask, meta: {title: ''}
  }, {
    path: '/checkTask', name: 'checkTask', component: checkTask, meta: {title: ''}
  }, {
    path: '/commandTabs', name: 'commandTabs', component: commandTabs, meta: {title: ''}
  }, {
    path: '/myCommand', name: 'myCommand', component: myCommand, meta: {title: ''}
  }, {
    path: '/createCommand/:pollingId', name: 'createCommand', component: createCommand, props: true, meta: {title: ''}
  }, {
    path: '/commandTaskTabs', name: 'commandTaskTabs', component: commandTaskTabs, meta: {title: ''},
    children: [
      { path: '', component: receiveCommandTask, name: '', meta: {title: ''} },
      {path: '/receiveCommandTask', name: 'receiveCommandTask', component: receiveCommandTask, meta: {title: ''} },
      {path: '/sendCommandTask', name: 'sendCommandTask', component: sendCommandTask, meta: {title: ''} }
    ]
  }, {
    path: '/commandDetail', name: 'commandDetail', component: commandDetail, meta: {title: ''}
  }, {
    path: '/transpondCommand', name: 'transpondCommand', component: transpondCommand, meta: {title: ''}
  }, {
    path: '/finishCommand', name: 'finishCommand', component: finishCommand, meta: {title: ''}
  }, {
    path: '/commandSearch', component: commandSearch, name: 'commandSearch', meta: {title: ''}
  }, {
    path: '/footprint', name: 'footprint', component: footprint, meta: {title: ''}
  }, {
    path: '/messageList', name: 'messageList', component: messageList, meta: {title: ''}
  }, {
    path: '/noticeList', name: 'noticeList', component: noticeList, meta: {title: ''}
  }, {
    path: '/createNotice', name: 'createNotice', component: createNotice, meta: {title: ''}
  }, {
    path: '/noticeDetail', name: 'noticeDetail', component: noticeDetail, meta: {title: ''}
  }]
});


const history = window.sessionStorage;
history.clear()
let historyCount = history.getItem('pageCount') * 1 || 0;
history.setItem('/', 0);

// 
router.beforeEach((to, from, next) => {
	const toIndex = history.getItem(to.path);
	const fromIndex = history.getItem(from.path);
	if (toIndex) {
	    if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
	      store.commit('updateDirection', 'slide-left')
	    } else {
	      store.commit('updateDirection', 'slide-right')
	    }
	} else {
	    ++historyCount;
	    history.setItem('pageCount', historyCount);
	    to.path !== '/' && history.setItem(to.path, historyCount);
	    store.commit('updateDirection', 'slide-left')
	}

	if(to.meta.title) {
    document.title = to.meta.title;
  }

	if (to.matched.length ===0) { //
    	next('/');
  }else{
	  //requiresAuth
    if (to.matched.some(record => record.meta.requiresAuth)) {
      if (store.state.token) {
        next();
      } else {
        next({
          path: '/',
          query: { redirect: to.fullPath }
        });
      }
    } else {
      next();
    }
  }
});

export default router;



