import router from '../../router'

export const goback = function(hide){
	router.go(-1)
}

// 
var openw=null,waiting=null;
/**
 * 
 * @param {URIString} id : url
 * @param {boolean} wa : 
 * @param {boolean} ns : 
 * @param {JSON} ws : Webview
 */
export const openView = function(id,wa,ns,ws){
	if(openw){//
		return null;
	}
	if(window.plus){
		wa&&(waiting=plus.nativeUI.showWaiting());
		ws=ws||{};
		ws.scrollIndicator||(ws.scrollIndicator='none');
		ws.scalable||(ws.scalable=false);
		var pre='';//'http://192.168.1.178:8080/h5/';
		openw=plus.webview.create(pre+id,id,ws);
		ns||openw.addEventListener('loaded',function(){//
//		setTimeout(function(){//
			openw.show(as);
			waiting&&waiting.close();
			waiting=null;
//		},200);
		},false);
		openw.addEventListener('close',function(){//
			openw=null;
		},false);
		return openw;
	}
	return null;
};

// H5 plus
function plusReady(){
	// 
	let ws = plus.webview.currentWebview();
	ws.setStyle({scrollIndicator:'none'});
	//ws.show();
	// Android
	plus.key.addEventListener('backbutton',function(){
		var currentRoute = router.currentRoute;
		console.log(currentRoute.path)
		if(currentRoute.path === '' || currentRoute.path === '/' || currentRoute.path === '/tab'){
			('iOS'==plus.os.name)?plus.nativeUI.confirm('', function(e){
				if(e.index>0){
					plus.runtime.quit();
				}
			}, 'HelloH5', ['','']):(confirm('')&&plus.runtime.quit());
		}else{
			goback()
		}
	},false);
}

if(window.plus){
	plusReady();
}else{
	document.addEventListener('plusready',plusReady,false);
}



