<template>
  <div class="cs_contain has_nav_container">
    <PubNavBarTo title="" gotoVueName="/tab"></PubNavBarTo>
    <div class="cs_search">
      <yd-flexbox>
        <yd-flexbox-item>
          <yd-cell-group>
            <yd-cell-item>
              <span slot="left"></span>
              <yd-input slot="right" v-model="showName.orgName" placeholder="" :readonly="true" @click.native="selectUserGroup"></yd-input>
            </yd-cell-item>
            <yd-cell-item>
              <span slot="left"></span>
              <yd-input slot="right" v-model="showName.projectItemName" placeholder="" :readonly="true" @click.native="selectProjectItem"></yd-input>
            </yd-cell-item>
          </yd-cell-group>
        </yd-flexbox-item>
        <div style="width: 60px;text-align: center;">
          <div class="huixin_search_btn" @click="search()"></div>
        </div>
      </yd-flexbox>
    </div>
    <div class="zlxj_main">
      <yd-tab height="32px" font-size="14px" ref="ydTab" horizontal-scroll :callback="tabFootPrint">
        <yd-tab-panel tabkey="realcheck" label="">
          <yd-infinitescroll :callback="loadDataList_realcheck" ref="infinitescrollRealcheck">
            <yd-list theme="4" slot="list">
              <yd-list-item v-for="(item, key) in checkList" :key="key" @click.native="findDetail(item,'realcheck')">
                <img slot="img" :src="item.filePath"/>
                <span slot="title">{{item.realname}}</span>
                <yd-list-other slot="other">
                  <ul>
                    <li class="list-price"><b>:</b>{{item.projectItem}}</li>
                    <li class="list-price"><b>:</b>{{item.zhuanghao}}</li>
                    <li class="list-del-price"><b>:</b>{{item.createTime}}</li>
                  </ul>
                </yd-list-other>
              </yd-list-item>
            </yd-list>
            <!--  -->
            <span slot="doneTip"></span>
          </yd-infinitescroll>
        </yd-tab-panel>
        <yd-tab-panel tabkey="selecheck" label="">
          <yd-infinitescroll :callback="loadDataList_selecheck" ref="infinitescrollSelecheck">
            <yd-list theme="4" slot="list">
              <yd-list-item v-for="(item, key) in selfCheckList" :key="key" @click.native="findDetail(item,'selecheck')">
                <img slot="img" :src="item.filePath"/>
                <span slot="title">{{item.realname}}</span>
                <yd-list-other slot="other">
                  <ul>
                    <li class="list-price"><b>:</b>{{item.projectItem}}</li>
                    <li class="list-price"><b>:</b>{{item.zhuanghao}}</li>
                    <li class="list-del-price"><b>:</b>{{item.createTime}}</li>
                  </ul>
                </yd-list-other>
              </yd-list-item>
            </yd-list>
            <!--  -->
            <span slot="doneTip"></span>
          </yd-infinitescroll>
        </yd-tab-panel>
        <yd-tab-panel tabkey="log" label="">
          <yd-infinitescroll :callback="loadDataList_log" ref="infinitescrollLog">
            <yd-list theme="4" slot="list">
              <yd-list-item v-for="(item, key) in logList" :key="key" @click.native="findDetail(item,'log')">
                <img slot="img" :src="item.filePath"/>
                <span slot="title">{{item.realname}}</span>
                <yd-list-other slot="other">
                  <ul>
                    <li class="list-price"><b>:</b>{{item.projectItem}}</li>
                    <li class="list-price"><b>:</b>{{item.zhuanghao}}</li>
                    <li class="list-del-price"><b>:</b>{{item.createTime}}</li>
                  </ul>
                </yd-list-other>
              </yd-list-item>
            </yd-list>
            <!--  -->
            <span slot="doneTip"></span>
          </yd-infinitescroll>
        </yd-tab-panel>
        <yd-tab-panel tabkey="command" label="" >
          <yd-infinitescroll :callback="loadDataList_command" ref="infinitescrollCommand">
            <yd-list theme="4" slot="list">
              <yd-list-item v-for="(item, key) in commandList" :key="key" @click.native="findDetail(item,'command')">
                <img slot="img" :src="item.filePath"/>
                <span slot="title">{{item.realname}}</span>
                <yd-list-other slot="other">
                  <ul>
                    <li class="list-price"><b>:</b>{{item.projectItem}}</li>
                    <li class="list-price"><b>:</b>{{item.zhuanghao}}</li>
                    <li class="list-del-price"><b>:</b>{{item.createTime}}</li>
                  </ul>
                </yd-list-other>
              </yd-list-item>
            </yd-list>
            <!--  -->
            <span slot="doneTip"></span>
          </yd-infinitescroll>
        </yd-tab-panel>
        <yd-tab-panel tabkey="polling" label="">
          <yd-infinitescroll :callback="loadDataList_polling" ref="infinitescrollPolling">
            <yd-list theme="4" slot="list">
              <yd-list-item v-for="(item, key) in pollingList" :key="key" @click.native="findDetail(item,'polling')">
                <img slot="img" :src="item.filePath"/>
                <span slot="title">{{item.realname}}</span>
                <yd-list-other slot="other">
                  <ul>
                    <li class="list-price"><b>:</b>{{item.projectItem}}</li>
                    <li class="list-price"><b>:</b>{{item.zhuanghao}}</li>
                    <li class="list-del-price"><b>:</b>{{item.createTime}}</li>
                  </ul>
                </yd-list-other>
              </yd-list-item>
            </yd-list>
            <!--  -->
            <span slot="doneTip"></span>
          </yd-infinitescroll>
        </yd-tab-panel>
      </yd-tab>
    </div>
    <!--  -->
    <yd-popup v-model="isShowUserGroup" position="center" width="90%">
      <vue-ztree :list.sync='userGroup' :func="ztreeNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
    <!--  -->
    <yd-popup v-model="isShowProjectItem" position="center" width="90%">
      <vue-ztree :list.sync='projectItem' :func="ztreeProjectItemNodeClick" :is-open='true'></vue-ztree>
    </yd-popup>
  </div>
</template>

<script>
import PubNavBarTo from '../../components/pub/NavBarTo'
import {getUserDepartZtree, getProjectItemListByOrgId} from '../../api/polling'
import {footprintPage} from '../../api/footprint'
export default {
  name: "footprint",
  data(){
    return{
      isShowProjectItem: false,
      isShowUserGroup: false,
      projectItem: [],
      userGroup: [],
      checkList: [],
      selfCheckList: [],
      logList: [],
      commandList: [],
      pollingList: [],
      showName: {
        orgName: '',
        projectItemName: ''
      },
      showValue:{
        departId:'',
        projectItemId: '',
        pageNo: 1,
        pageSize: 15
      }
    }
  },
  components:{
    PubNavBarTo: PubNavBarTo
  },
  methods:{
    selectUserGroup: function () {
      this.isShowUserGroup = true
      this.initUserGroupZtree()
    },
    initUserGroupZtree: function () {
      getUserDepartZtree(res => {
        this.userGroup = res.data
      })
    },
    ztreeNodeClick: function (m) {
      m.hover = false
      let node = JSON.parse(JSON.stringify(m))
      if (node.orgTpye === '4') {
        this.showName.orgName = m.name
        this.showValue.departId = m.id
        this.isShowUserGroup = false
      } else {
        this.$dialog.toast({
          mes: '',
          timeout: 1500
        })
      }
    },
    selectProjectItem: function () {
      this.isShowProjectItem = true
      this.initProjectItemZtree(this.orgId)
    },
    ztreeProjectItemNodeClick: function (m) {
      m.hover = false
      this.showName.projectItemName = m.name
      this.showValue.projectItemId = m.id
      this.isShowProjectItem = false
    },
    initProjectItemZtree: function (orgId) {
      getProjectItemListByOrgId({orgId: orgId}, res=>{
        this.projectItem = res.data
      })
    },
    tabFootPrint(label, tabkey){
      if(tabkey===''){
        this.checkList=[]
        this.$refs.infinitescrollRealcheck.$emit('ydui.infinitescroll.reInit')
        this.loadDataList_realcheck()
      }else if(tabkey===''){
        this.selfCheckList=[]
        this.$refs.infinitescrollSelecheck.$emit('ydui.infinitescroll.reInit')
        this.loadDataList_selecheck()
      }else if(tabkey===''){
        this.logList=[]
        this.$refs.infinitescrollLog.$emit('ydui.infinitescroll.reInit')
        this.loadDataList_log()
      }else if(tabkey===''){
        this.commandList=[]
        this.$refs.infinitescrollCommand.$emit('ydui.infinitescroll.reInit')
        this.loadDataList_command()
      }else if(tabkey===''){
        this.pollingList=[]
        this.$refs.infinitescrollCommand.$emit('ydui.infinitescroll.reInit')
        this.loadDataList_polling()
      }
    },
    loadDataList_realcheck:function(){
      footprintPage({pageNo: this.showValue.pageNo, pageSize: this.showValue.pageSize, startTime: null, endTime:null, projectid: this.showValue.projectItemId, searchname: null, type: 'realcheck', orderby:'1'}, res=>{
        if(res.ok){
          if(res.data && res.data.data.length > 0){
            this.checkList = this.checkList.concat(res.data.data)
            /*  */
            this.$refs.infinitescrollRealcheck.$emit('ydui.infinitescroll.finishLoad');
            this.showValue.pageNo++
          }else{
            /*  */
            this.$refs.infinitescrollRealcheck.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    loadDataList_selecheck:function(){
      footprintPage({pageNo: this.showValue.pageNo, pageSize: this.showValue.pageSize, startTime: null, endTime:null, projectid: this.showValue.projectItemId, searchname: null, type: 'selecheck', orderby:'1'}, res=>{
        if(res.ok){
          if(res.data && res.data.data.length > 0){
            this.selfCheckList = this.selfCheckList.concat(res.data.data)
            /*  */
            this.$refs.infinitescrollSelecheck.$emit('ydui.infinitescroll.finishLoad');
            this.showValue.pageNo++
          }else{
            /*  */
            this.$refs.infinitescrollSelecheck.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    loadDataList_log:function(){
      footprintPage({pageNo: this.showValue.pageNo, pageSize: this.showValue.pageSize, startTime: null, endTime:null, projectid: this.showValue.projectItemId, searchname: null, type: 'log', orderby:'1'}, res=>{
        if(res.ok){
          if(res.data && res.data.data.length > 0){
            this.logList = this.logList.concat(res.data.data)
            /*  */
            this.$refs.infinitescrollLog.$emit('ydui.infinitescroll.finishLoad');
            this.showValue.pageNo++
          }else{
            /*  */
            this.$refs.infinitescrollLog.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    loadDataList_command:function(){
      footprintPage({pageNo: this.showValue.pageNo, pageSize: this.showValue.pageSize, startTime: null, endTime:null, projectid: this.showValue.projectItemId, searchname: null, type: 'command', orderby:'1'}, res=>{
        if(res.ok){
          if(res.data && res.data.data.length > 0){
            this.commandList = this.commandList.concat(res.data.data)
            /*  */
            this.$refs.infinitescrollCommand.$emit('ydui.infinitescroll.finishLoad');
            this.showValue.pageNo++
          }else{
            /*  */
            this.$refs.infinitescrollCommand.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    loadDataList_polling:function(){
      footprintPage({pageNo: this.showValue.pageNo, pageSize: this.showValue.pageSize, startTime: null, endTime:null, projectid: this.showValue.projectItemId, searchname: null, type: 'polling', orderby:'1'}, res=>{
        if(res.ok){
          if(res.data && res.data.data.length > 0){
            this.pollingList = this.pollingList.concat(res.data.data)
            /*  */
            this.$refs.infinitescrollPolling.$emit('ydui.infinitescroll.finishLoad');
            this.showValue.pageNo++
          }else{
            /*  */
            this.$refs.infinitescrollPolling.$emit('ydui.infinitescroll.loadedDone');
          }
        }
      })
    },
    search:function () {
      this.showValue.pageNo=1
      let currentIndex=this.$refs.ydTab.currentIndex
      if(currentIndex===0){
        this.checkList=[]
        this.$refs.infinitescrollRealcheck.$emit('ydui.infinitescroll.reInit')
        this.loadDataList_realcheck()
      }else if(currentIndex===1){
        this.selfCheckList=[]
        this.$refs.infinitescrollSelecheck.$emit('ydui.infinitescroll.reInit')
        this.loadDataList_selecheck()
      }else if(currentIndex===2){
        this.logList=[]
        this.$refs.infinitescrollLog.$emit('ydui.infinitescroll.reInit')
        this.loadDataList_log()
      }else if(currentIndex===3){
        this.commandList=[]
        this.$refs.infinitescrollCommand.$emit('ydui.infinitescroll.reInit')
        this.loadDataList_command()
      }else if(currentIndex===4){
        this.pollingList=[]
        this.$refs.infinitescrollCommand.$emit('ydui.infinitescroll.reInit')
        this.loadDataList_polling()
      }
    },
    findDetail:function (item, state) {
      if(state==='realcheck'){
        this.$router.push({
          path: '/processDetail',
          query: {
            processId: item.processInfoID,
            checkState: item.state,
            tabState: 'search',
            checkType: '0'
          }
        })
      }else if(state==='selecheck'){
        this.$router.push({
          path: '/processDetail',
          query: {
            processId: item.processInfoID,
            checkState: item.state,
            tabState: 'search',
            checkType: '1'
          }
        })
      }else if(state==='log'){
        this.$router.push('/supervisorLogDetail/' + item.id)
      }else if(state==='command'){
        this.$router.push({
          path: '/commandDetail',
          query: {
            commandId: item.commandId,
            tabState: 'send',
            issolve: 1
          }
        })
      }else if(state==='polling'){
        this.$router.push('/pollingDetail/' + item.id)
      }
    }
  },
  created(){
    this.loadDataList_realcheck()
  }
}
</script>

<style scoped>

</style>



