var back = null;
function geolocationPlusReady(url){
	plus.geolocation.getCurrentPosition( geoInfo, function (e) {
    switch(e.code) {
      case e.PERMISSION_DENIED:
        plus.nativeUI.toast('');
        break;
      case e.POSITION_UNAVAILABLE:
        plus.nativeUI.toast('');
        break;
      case e.TIMEOUT:
        plus.nativeUI.toast('');
        break;
      case e.UNKNOWN_ERROR:
        plus.nativeUI.toast('');
        break;
    }
		back({success: false, msg: ""+e.message});
	},{geocode:true, enableHighAcuracy: true, maximumAge: 3000});
}

function geoInfo(position) {
	var str = "";
	str += ""+position.addresses+"\n";//
	str += ""+position.coordsType+"\n";
	var timeflag = position.timestamp;//
	str += ""+timeflag+"\n";
	var codns = position.coords;//
	var lat = codns.latitude;//
	str += ""+lat+"\n";
	if(codns.longitude===undefined || codns.longitude==="" || codns.longitude===null){
	  plus.nativeUI.toast('');
  }else{
    var longt = codns.longitude;//
    str += ""+longt+"\n";
    var alt = codns.altitude;//
    str += ""+alt+"\n";
    var accu = codns.accuracy;//
    str += ""+accu+"\n";
    var altAcc = codns.altitudeAccuracy;//
    str += ""+altAcc+"\n";
    var head = codns.heading;//
    if(isNaN(head) || head===null){
      head="";
    }
    str += ""+head+"\n";
    var sped = codns.speed;//
    str += ""+sped;
    console.log(str);
  }
  back(position)
}

export const getGeolocation = function(callback){
	back = callback;
	if(window.plus){
		geolocationPlusReady();
	}else{
		document.addEventListener('plusready', geolocationPlusReady, false);
	}
}



