import router from '../../router'
import {messagePush} from '../../assets/plus/push'

let push_title, push_content, push_payload;
document.addEventListener( "plusready", function(){
  plus.runtime.setBadgeNumber(0);
  plus.push.setAutoNotification(true);
  // 
  plus.push.addEventListener( "click", function( msg ) {
    /*
   
       */
    //
    messageHerf(msg)

  }, false );
  // 
  plus.push.addEventListener( "receive", function( msg ) {
    //
    push_content=msg.content;
    push_title=msg.payload.type;
    push_payload=msg.payload;
    messagePush('create', msg.content, msg.payload, msg.payload.type, function () {

    });

  }, false );

}, false );

//app
document.addEventListener("newintent", function() {
  openWebviewFromOthers();
});


/**  ***/
function messageHerf(msg){
  /**/
  var content=msg.content;
  var contentArr=content.split(',');
  var id=contentArr[contentArr.length-1];
  if(content.indexOf('')>0){
    //
    router.push({
      path: '/checkTask',
      query: {
        processId: id,
        checkState: '1',
        tabState: 'receive',
        checkType: '1'
      }
    })
  }else if(content.indexOf('~')>0){
    router.push({
      path: '/checkTask',
      query: {
        processId: id,
        checkState: '1',
        tabState: 'task',
        checkType: '0'
      }
    })
  }else if(content.indexOf('~')>0){
    //
    router.push({
      path: '/selfCheckTask',
      query: {
        processId: id,
        checkState: '1',
        tabState: 'task',
        checkType: '0'
      }
    })
  }else if(content.indexOf('')>0){
    router.push({
      path: '/commandDetail',
      query: {
        commandId: id,
        tabState: '0',
        issolve: '0'
      }
    })
  }
  //app
  messagePush('remove', push_title, push_content, push_payload);
}

/**  app ***/
function openWebviewFromOthers(){
  var args = plus.runtime.arguments;
  if(args) {
    messageHerf(args);
  }
}



